'use strict'

import Vue from 'vue'
import VueResource from 'vue-resource'
import { Buffer } from 'buffer'

Vue.use(VueResource)

const keynoteBaseWidth = keynote.width
const keynoteBaseHeight = keynote.height

export default {
    async loadLocalFile (url) {
        try {
            const response = await Vue.http.get(url, {responseType: 'arraybuffer'})
            return response.body
        } catch (err) {
            //  body 
            if (err.status === 0 && err.body instanceof ArrayBuffer) {
                return err.body
            } else {
                throw err
            }
        }
    },
    arrayBufferToJson (arrayBuffer) {
        if (arrayBuffer instanceof ArrayBuffer) {
            const buffer = Buffer.from(arrayBuffer)
            return JSON.parse(buffer.toString())
        }
        throw new Error('format error')
    },
    calcImgSize (url, scale, cb) {
        const img = new Image()
        img.addEventListener('load', () => {
            const size = {
                width: this.pxToPercentageWidth(img.width * scale),
                height: this.pxToPercentageHeight(img.height * scale)
            }
            cb(size)
        }, false)
        img.src = url
    },
    // 
    pxToPercentageWidth (pxNumber) {
        return `${100 * pxNumber / keynoteBaseWidth}%`
    },
    // 
    pxToPercentageHeight (pxNumber) {
        return `${100 * pxNumber / keynoteBaseHeight}%`
    },
    //  vw 
    pxToVWNumber (pxNumber) {
        return 100 * pxNumber / keynoteBaseWidth
    },
    toChar (num) {
        return String.fromCharCode(num + 65)
    }
}



