'use strict'

export default {
    log (...contents) {
        let date = new Date()
        let time = [date.getHours(), date.getMinutes(), date.getSeconds()].join(':')
        time += `.${date.getMilliseconds()}`

        contents.forEach((content) => {
            // todo  BoxBridge  log 
            let temp = content
            if (content instanceof Error) {
                temp = `${content.name}:${content.message}`
            } else if (typeof content === 'string') {
                temp = content
            } else {
                temp = JSON.stringify(content)
            }
            console.log(`[${time}] ${temp}`)
        })
    }
}



