<template>
    <div class="stage-option-container" :style="styles" :class="{hide: hide}">
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'stage-option',
        props: {
            index: {
                type: Number,
                required: true
            }
        },
        computed: {
            ...mapState('stage', [
                'optionsImgSize'
            ]),
            ...mapGetters('stage', [
                'optionsReady',
                'answer',
                'options'
            ]),
            ...mapGetters([
                'isAfterInteractionSolution'
            ]),
            styles () {
                const styles = {}
                if (this.optionsReady) {
                    const option = this.options[this.index]
                    const optionImgSize = this.optionsImgSize[this.index]
                    styles.top = option.top
                    styles.left = option.left
                    styles.backgroundImage = `url('${option.imgUrl}')`
                    styles.width = optionImgSize.width
                    styles.height = optionImgSize.height
                }
                return styles
            },
            hide () {
                if (this.optionsReady && this.isAfterInteractionSolution) {
                    return this.answer.correctOptionIndex === this.index
                }
            }
        }
    }
</script>

<style lang="less" scoped>

    .stage-option-container {
        position: absolute;
        background-repeat: no-repeat;
        background-size: 100% 100%;

        &.hide {
            display: none;
        }
    }

</style>



