'use strict'
import BoxBridge from '@tutor/box-bridge'
import logService from '../services/log-service'

const ClientApi = BoxBridge.ClientApi

function constructErrMsg (prefix, params, err) {
    return `${prefix}, params=${JSON.stringify(params)}, err=${JSON.stringify(err)}`
}

const clientService = {
    emit (eventName, eventData = {}) {
        return new Promise((resolve, reject) => {
            const params = {eventName, eventData}
            ClientApi.emit(params, (err, result) => {
                if (err) {
                    logService.log(constructErrMsg('emit event failed', params, err))
                    reject(new Error(err))
                    return
                }
                resolve(result)
            })
        })
    },
    createStokePage (pageCount = 1) {
        return new Promise((resolve, reject) => {
            const params = {pageCount}
            ClientApi.createStokePage(params, (err, result) => {
                if (err) {
                    logService.log(constructErrMsg('create page failed', params, err))
                    reject(new Error(err))
                    return
                }
                resolve(result)
            })
        })
    },
    toggleStrokePageVisible (strokePageId, visible) {
        return new Promise((resolve, reject) => {
            const params = {strokePageId, visible}
            ClientApi.toggleStrokePageVisible(params, (err, toggleResult) => {
                if (err) {
                    logService.log(constructErrMsg('toggle stroke page visible failed', params, err))
                    reject(new Error(err))
                    return
                }
                resolve(toggleResult.result)
            })
        })
    },
    toggleForumEnabled (enable) {
        return new Promise((resolve, reject) => {
            const params = {enable}
            ClientApi.toggleForumEnabled(params, (err, toggleResult) => {
                if (err) {
                    logService.log(constructErrMsg('toggle forum enabled failed', params, err))
                    reject(new Error(err))
                    return
                }
                resolve(toggleResult.result)
            })
        })
    },
    toggleToolbarItemsEnabled (enabledIds, disabledIds) {
        return new Promise((resolve, reject) => {
            const params = {enabledIds, disabledIds}
            ClientApi.toggleToolbarItemsEnabled(params, (err, toggleResult) => {
                if (err) {
                    logService.log(constructErrMsg('toggle toolbar items enabled failed', params, err))
                    reject(new Error(err))
                    return
                }
                resolve(toggleResult.result)
            })
        })
    }
}

export default clientService



