'use strict'

import Vue from 'vue'
import environment from './environment'
import * as modes from '../constants/mode'
import BoxBridge from '@tutor/box-bridge'
import VueResource from 'vue-resource'

Vue.use(VueResource)

const prefix = `${environment.apiPrefix}tutor-live-box-drag-drop/api/rooms{/roomId}/drag-boxed`
const MockServerApi = BoxBridge.MockServerApi

const boxService = Vue.resource(prefix, {}, {
    _init: {
        method: 'POST',
        url: `${prefix}`
    },
    _startInteraction: {
        method: 'POST',
        url: `${prefix}{/keynotePageId}/quizzes`
    },
    _endInteraction: {
        method: 'POST',
        url: `${prefix}{/keynotePageId}/ended-quizzes`
    },
    _jumpToQuestion: {
        method: 'POST',
        url: `${prefix}{/keynotePageId}/jump-to-question`
    },
    _jumpToSolution: {
        method: 'POST',
        url: `${prefix}{/keynotePageId}/jump-to-solution`
    }
})

function mapByMode (mode, liveCastModeCb, devModeCb) {
    switch (mode) {
        case modes.LIVE_CAST:
            return liveCastModeCb()
        case modes.DEV:
            return devModeCb()
        case modes.PLAYBACK:
            return Promise.resolve({})
    }
}

boxService.init = function (mode, roomId, appId, appVersion, appConfigId, keynotePageId, questionStrokePageId) {
    return mapByMode(mode, () => {
        const params = {roomId}
        const body = {appId, appVersion, appConfigId, keynotePageId, questionStrokePageId}
        return this._init(params, body).then(response => {return response.json()})
    }, () => {
        return MockServerApi.execApi('init', ...[roomId, appId, appVersion, appConfigId, keynotePageId, questionStrokePageId])
    })
}

boxService.startInteraction = function (mode, roomId, keynotePageId) {
    mapByMode(mode, () => {
        const params = {roomId, keynotePageId}
        return this._startInteraction(params, {}).then(response => {return response.json()})
    }, () => {
        return MockServerApi.execApi('startInteraction', ...[roomId, keynotePageId])
    })
}

boxService.endInteraction = function (mode, roomId, keynotePageId, solutionStrokePageId) {
    mapByMode(mode, () => {
        const params = {roomId, keynotePageId}
        const body = {solutionStrokePageId}
        return this._endInteraction(params, body).then(response => {return response.json()})
    }, () => {
        return MockServerApi.execApi('endInteraction', ...[roomId, keynotePageId, solutionStrokePageId])
    })
}

boxService.jumpToQuestion = function (mode, roomId, keynotePageId) {
    mapByMode(mode, () => {
        const params = {roomId, keynotePageId}
        return this._jumpToQuestion(params, {}).then(response => {return response.json()})
    }, () => {
        return MockServerApi.execApi('jumpToQuestion', ...[roomId, keynotePageId])
    })
}

boxService.jumpToSolution = function (mode, roomId, keynotePageId) {
    mapByMode(mode, () => {
        const params = {roomId, keynotePageId}
        return this._jumpToSolution(params, {}).then(response => {return response.json()})
    }, () => {
        return MockServerApi.execApi('jumpToSolution', ...[roomId, keynotePageId])
    })
}

export default boxService



