<template>
    <div class="stage-title-container" :style="styles">
        <span v-if="isTextTitle">{{this.title.content}}</span>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'stage-title',
        computed: {
            ...mapState('stage', [
                'titleImgSize'
            ]),
            ...mapGetters('stage', [
                'titleReady',
                'isImgTitle',
                'isTextTitle',
                'title'
            ]),
            styles () {
                const styles = {}
                if (this.titleReady) {
                    if (this.isImgTitle) {
                        styles.top = this.title.top
                        styles.left = this.title.left
                        styles.backgroundImage = `url('${this.title.imgUrl}')`
                        styles.width = this.titleImgSize.width
                        styles.height = this.titleImgSize.height
                    }
                    if (this.isTextTitle) {
                        styles.top = this.title.top
                        styles.left = this.title.left
                        styles.fontSize = `${this.title.fontSize}vw`
                        Object.assign(styles, this.title.styles)
                    }
                }
                return styles
            }
        }
    }
</script>

<style lang="less" scoped>

    .stage-title-container {
        position: absolute;
        background-repeat: no-repeat;
        background-size: 100% 100%;

        span {
            user-select: none;
        }
    }

</style>



