'use strict'

export const BEFORE_INTERACTION = 'beforeInteraction'
export const INTERACTING = 'interacting'
export const AFTER_INTERACTION_SOLUTION = 'afterInteractionSolution'
export const AFTER_INTERACTION_QUESTION = 'afterInteractionQuestion'
export const validStatus = function (status) {
    return [BEFORE_INTERACTION, INTERACTING, AFTER_INTERACTION_SOLUTION, AFTER_INTERACTION_QUESTION].includes(status)
}

const STATUS = [{
    value: 0,
    name: BEFORE_INTERACTION
}, {
    value: 1,
    name: INTERACTING
}, {
    value: 2,
    name: AFTER_INTERACTION_SOLUTION
}, {
    value: 3,
    name: AFTER_INTERACTION_QUESTION
}]

export const fromValue = function (value) {
    const status = STATUS.find(status => status.value === value)
    return status ? status.name : ''
}



