'use strict'

const environment = {
    env: 'online',
    apiPrefix: 'https://www.yuanfudao.com',
    galleryPrefix: 'https://gallery.fbcontent.cn'
}

if (location.hostname.includes('local')) {
    environment.env = 'local'
    environment.apiPrefix = 'https://ke.yuanfudao.ws'
    environment.galleryPrefix = 'https://ytkgallery.yuanfudao.ws'
} else if (location.hostname.includes('ws') || location.hostname.includes('test')) {
    environment.env = 'test'
    environment.apiPrefix = 'https://ke.yuanfudao.ws'
    environment.galleryPrefix = 'https://ytkgallery.yuanfudao.ws'
}

if (environment.apiPrefix[environment.apiPrefix.length - 1] !== '/') {
    environment.apiPrefix += '/'
}

export default environment



