'use strict'

import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './mutation-types'
import teacher from './modules/teacher'
import student from './modules/student'
import stage from './modules/stage'
import * as userRoles from '../constants/user-role'
import * as status from '../constants/web-app-status'
import * as modes from '../constants/mode'

Vue.use(Vuex)

const state = {
    status: undefined,
    questionStrokePageId: undefined,
    solutionStrokePageId: undefined,
    bizSyncing: false,
    firstBizSynced: false // biz
}

const getters = {
    appId: state => +state.route.query.appId,
    appVersion: state => +state.route.query.appVersion,
    appConfigId: state => state.route.query.appConfigId,
    roomId: state => +state.route.query.roomId,
    userId: state => +state.route.query.userId,
    userRole: state => state.route.query.userRole,
    mode: state => state.route.query.mode,
    keynotePageId: state => +state.route.query.keynotePageId,
    firstStrokePageId: state => +state.route.query.firstStrokePageId,
    withBizWhenLoad: state => state.route.query.hasOwnProperty('withBiz'),
    isLiveCastMode: (state, getters) => getters.mode === modes.LIVE_CAST,
    isPlaybackMode: (state, getters) => getters.mode === modes.PLAYBACK,
    isDevMode: (state, getters) => getters.mode === modes.DEV,
    isBeforeInteraction: state => state.status === status.BEFORE_INTERACTION,
    isInteracting: state => state.status === status.INTERACTING,
    isAfterInteractionQuestion: state => state.status === status.AFTER_INTERACTION_QUESTION,
    isAfterInteractionSolution: state => state.status === status.AFTER_INTERACTION_SOLUTION,
    isTeacher: (state, getters) => getters.userRole === userRoles.TEACHER,
    isStudent: (state, getters) => getters.userRole === userRoles.STUDENT
}

const mutations = {
    [types.SET_STATUS] (state, status) {
        state.status = status
    },
    [types.SET_QUESTION_STROKE_PAGE_ID] (state, questionStrokePageId) {
        state.questionStrokePageId = questionStrokePageId
    },
    [types.SET_SOLUTION_STROKE_PAGE_ID] (state, solutionStrokePageId) {
        state.solutionStrokePageId = solutionStrokePageId
    },
    [types.TOGGLE_BIZ_SYNCING] (state, bizSyncing) {
        state.bizSyncing = bizSyncing
    },
    [types.TOGGLE_FIRST_BIZ_SYNCED] (state, firstBizSynced) {
        state.firstBizSynced = firstBizSynced
    }
}

const actions = {
    setFirstBizSynced ({commit}) {
        commit(types.TOGGLE_FIRST_BIZ_SYNCED, true)
    }
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
    modules: {
        teacher,
        student,
        stage
    },
    strict: process.env.NODE_ENV !== 'production'
})



