<template>
    <div class="web-app">
        <div class="left-side" :class="{split: isInteracting}">
            <div class="teacher-stage-container">
                <teacher-web-app-stage></teacher-web-app-stage>
            </div>
            <div class="answer-distribution-container">
                <answer-distribution></answer-distribution>
            </div>
        </div>
        <div class="right-side">
            <student-list></student-list>
        </div>
        <status-bar></status-bar>
    </div>
</template>

<script>
    import BoxBridge from '@tutor/box-bridge'
    import clientService from '../services/client-service'
    import * as webAppEvents from '../constants/web-app-event'
    import * as clientEvents from '../constants/client-event'
    import { mapState, mapGetters, mapActions } from 'vuex'
    import StatusBar from './StatusBar'
    import TeacherWebAppStage from './TeacherWebAppStage'
    import AnswerDistribution from './AnswerDistribution'
    import StudentList from './StudentList'

    export default {
        name: 'teacher-web-app',
        components: {
            StudentList,
            AnswerDistribution,
            TeacherWebAppStage,
            StatusBar
        },
        computed: {
            ...mapState([
                'firstBizSynced'
            ]),
            ...mapGetters([
                'withBizWhenLoad',
                'isInteracting'
            ]),
            ...mapGetters('stage', {
                stageReady: 'ready'
            })
        },
        watch: {
            stageReady: function (isStageReady) {
                if (!isStageReady) {
                    return
                }
                clientService.emit(webAppEvents.JS_READY)
                if (!this.withBizWhenLoad) {
                    this.init()
                }
            },
            isInteracting: function (isInteracting) {
                if (isInteracting) {
                    // 
                    this.createSolutionStrokePage()
                }
            }
        },
        methods: {
            ...mapActions([
                'setFirstBizSynced'
            ]),
            ...mapActions('teacher', [
                'init',
                'syncBiz',
                'createSolutionStrokePage',
                'onStrokePageCreated',
                'toggleClientWidgetsEnabledAndStrokePageVisible'
            ])
        },
        mounted () {
            const ClientApi = BoxBridge.ClientApi
            ClientApi.on(clientEvents.ACTIVE, () => {
                this.toggleClientWidgetsEnabledAndStrokePageVisible()
            })

            ClientApi.on(clientEvents.DESTROY, () => {
                ClientApi.destroy()
                clientService.emit(webAppEvents.END)
            })

            ClientApi.on(clientEvents.BIZ_CREATED, async (biz) => {
                await this.syncBiz(biz)
                if (this.firstBizSynced) {
                    this.toggleClientWidgetsEnabledAndStrokePageVisible()
                } else {
                    this.setFirstBizSynced()
                    clientService.emit(webAppEvents.READY)
                }
            })

            ClientApi.on(clientEvents.STROKE_PAGE_CREATED, ({currentStrokePageIds}) => {
                this.onStrokePageCreated(currentStrokePageIds)
            })
        }
    }
</script>

<style lang="less" scoped>
    @import (reference) '../styles/variables.less';

    @stage-shrink-percentage-remainder: 100% - @stage-shrink-percentage;

    .web-app {
        overflow: hidden;

        .left-side {
            float: left;
            width: 100%;
            height: 100%;
            overflow: hidden;

            .teacher-stage-container {
                height: 100%;
            }

            .answer-distribution-container {
                height: calc(@stage-shrink-percentage-remainder ~'-' @status-bar-width);
            }

            &.split {
                width: @stage-shrink-percentage;

                .teacher-stage-container {
                    height: @stage-shrink-percentage;
                }
            }
        }

        .right-side {
            float: left;
            width: @stage-shrink-percentage-remainder;
            height: calc(100% ~'-' @status-bar-width);
        }
    }

</style>



