<template>
    <div class="student-list-container">
        <div v-for="(student,index) in students" :key="index" class="student-list-item">
            <div class="avatar">
                <img :src="toImgUrl(student.avatar)"/>
            </div>
            <div class="nickname">{{student.nickname}}</div>
            <div class="answer" :class="{correct: answer.correctOptionIndex === student.answer}">
                {{toChar(student.answer)}}
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState, mapGetters, mapActions } from 'vuex'
    import galleryImg from '../filters/gallery-img-filter'
    import util from '../services/util'

    export default {
        name: 'student-list',
        data () {
            return {
                timer: null
            }
        },
        filters: {
            galleryImg
        },
        computed: {
            ...mapGetters([
                'isInteracting',
                'isAfterInteractionSolution'
            ]),
            ...mapGetters('stage', [
                'answer'
            ]),
            ...mapState('teacher', [
                'students'
            ])
        },
        watch: {
            isInteracting: function (isInteracting) {
                if (isInteracting && !this.timer) {
                    const fetch = () => {
                        this.timer = setTimeout(() => {
                            this.getStudentsStat().then(() => {
                                fetch()
                            })
                        }, 2000)
                    }
                    fetch()
                }
            },
            isAfterInteractionSolution: function (isAfterInteractionSolution) {
                if (isAfterInteractionSolution && this.timer) {
                    clearTimeout(this.timer)
                    this.timer = null
                }
            }
        },
        methods: {
            ...mapActions('teacher', [
                'getStudentsStat'
            ]),
            toChar (num) {
                return util.toChar(num)
            },
            toImgUrl (imgId) {
                return galleryImg(imgId)
            }
        }
    }
</script>

<style lang="less" scoped>
    @import (reference) '../styles/variables.less';

    .student-list-container {
        height: 100%;
        border-left: 1px solid #ccc;
        overflow-y: scroll;

        .student-list-item {
            display: flex;
            align-items: center;
            padding: 10px 20px;
            border-bottom: 1px solid #ccc;

            .avatar {
                img {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                }
            }

            .nickname {
                flex-grow: 1;
                max-width: 70%;
                overflow: hidden;
                padding-left: 10px;
                font-size: 12px;
                color: #999;
            }

            .answer {
                font-size: 16px;
                padding-left: 10px;
                color: @wrong-color;

                &.correct {
                    color: @correct-color;
                }
            }
        }
    }

</style>



