<template>
    <div class="distribution-container">
        <div class="title"></div>
        <div class="answer-container" v-for="(answerRatio, index) in answerDistribution" :key="index"
             :class="{correct: answer.correctOptionIndex === index}">
            <div class="option">{{toChar(index)}}</div>
            <div class="distribution-chart">
                <div class="distribution-bar" :style="toWidth(answerRatio)"></div>
            </div>
            <div class="distribution-value">{{answerRatio.ratio | percentage()}}</div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import percentage from '../filters/percentage-filter'
    import util from '../services/util'

    export default {
        name: 'answer-distribution',
        filters: {
            percentage
        },
        computed: {
            ...mapGetters('stage', [
                'answer'
            ]),
            ...mapGetters('teacher', [
                'answerDistribution'
            ])
        },
        methods: {
            toChar (num) {
                return util.toChar(num)
            },
            toWidth (answerRatio) {
                return {
                    width: percentage(answerRatio.ratio)
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    @import (reference) '../styles/variables.less';

    .distribution-container {
        display: flex;
        flex-flow: row wrap;
        height: 100%;
        padding: 10px;
        border-top: 1px solid #ccc;

        .title {
            width: 100%;
            height: 20%;
            font-size: 14px;
            color: #999;
        }

        .answer-container {
            width: 50%;
            height: 20%;
            display: flex;
            align-items: center;
            font-size: 14px;

            .option {
                width: 20%;
                color: @wrong-color;
            }

            .distribution-chart {
                flex-grow: 1;
                display: flex;
                overflow: hidden;
                height: 30%;
                background-color: #e9ecef;

                .distribution-bar {
                    background-color: @wrong-color;
                    transition: width .6s ease;
                }
            }

            .distribution-value {
                width: 20%;
                color: #999;
                padding-left: 10px;
                font-size: 8px;
            }

            &.correct {

                .option {
                    color: @correct-color;
                }

                .distribution-bar {
                    background-color: @correct-color;
                }
            }
        }
    }
</style>



