<template>
    <div class="status-bar">
        <div class="status-bar-content text-muted">
            <span v-if="isBeforeInteraction"></span>
            <span v-else-if="isInteracting">
                 {{now | relativeTime(interactionStartTime)}}{{completedCount}}/{{totalCount}} 
            </span>
            <span v-else>
                
                <span class="answer-distribution"
                      v-for="(answerRatio, index) in answerDistribution" :key="index"
                      :class="{correct: answer.correctOptionIndex === index}">
                    {{toChar(index)}}:{{answerRatio.ratio | percentage()}}
                </span>
            </span>
        </div>
        <button type="button" class="status-bar-btn btn btn-sm btn-light ml-auto"
                v-if="isBeforeInteraction"
                :disabled="btnDisabled" @click="onStartInteraction">
        </button>
        <button type="button" class="status-bar-btn btn btn-sm btn-light ml-auto"
                v-if="isInteracting"
                :disabled="btnDisabled" @click="onEndInteraction">
        </button>
        <button type="button" class="status-bar-btn btn btn-sm btn-light ml-auto"
                v-if="isAfterInteractionSolution"
                :disabled="btnDisabled" @click="jumpToQuestion">
        </button>
        <button type="button" class="status-bar-btn btn btn-sm btn-light ml-auto"
                v-if="isAfterInteractionQuestion"
                :disabled="btnDisabled" @click="jumpToSolution">
        </button>
    </div>
</template>

<script>
    import { mapState, mapGetters, mapActions } from 'vuex'
    import relativeTime from '../filters/relative-time-filter'
    import percentage from '../filters/percentage-filter'
    import util from '../services/util'

    export default {
        name: 'status-bar',
        filters: {
            relativeTime,
            percentage
        },
        data () {
            return {
                now: null,
                timer: null
            }
        },
        computed: {
            ...mapState('teacher', [
                'bizCreating',
                'waitingBiz',
                'interactionStartTime',
                'totalCount'
            ]),
            ...mapGetters([
                'isBeforeInteraction',
                'isInteracting',
                'isAfterInteractionQuestion',
                'isAfterInteractionSolution'
            ]),
            ...mapGetters('stage', [
                'answer'
            ]),
            ...mapGetters('teacher', [
                'completedCount',
                'answerDistribution'
            ]),
            btnDisabled () {
                return this.bizCreating || this.waitingBiz
            }
        },
        methods: {
            ...mapActions('teacher', [
                'startInteraction',
                'endInteraction',
                'jumpToQuestion',
                'jumpToSolution'
            ]),
            ...mapActions('stage', [
                'updateStageShrink'
            ]),
            toChar (num) {
                return util.toChar(num)
            },
            async onStartInteraction () {
                const success = await this.startInteraction()
                if (!success) {
                    return
                }
                this.updateStageShrink(0.7)
                this.now = new Date().valueOf()
                const refresh = () => {
                    this.timer = setTimeout(() => {
                        this.now = new Date().valueOf()
                        refresh()
                    }, 1000)
                }
                refresh()
            },
            async onEndInteraction () {
                const success = await this.endInteraction()
                if (!success) {
                    return
                }
                this.updateStageShrink(1)
                if (this.timer) {
                    clearTimeout(this.timer)
                    this.timer = null
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    @import (reference) '../styles/variables.less';

    .status-bar {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        height: @status-bar-width;
        font-size: 12px;
        background-color: #ecf5ff;

        .status-bar-content {
            margin-left: 10px;
            font-size: 10px;
            line-height: @status-bar-width;

            .answer-distribution {
                color: @wrong-color;
                padding-left: 5px;

                &.correct {
                    color: @correct-color;
                }
            }
        }

        .status-bar-btn {
            margin: 2px;
            font-size: 10px;
        }
    }

</style>



