'use strict'

import logService from '../../services/log-service'
import util from '../../services/util'
import * as types from '../mutation-types'

function constructLocalFileUrl (rootGetters, fileName) {
    return `../config.${rootGetters.appId}.${rootGetters.appConfigId}/${fileName}`
}

const state = {
    config: {},
    shrink: 1,
    titleImgSize: null,
    optionsImgSize: [],
    answerImgSize: null
}

const getters = {
    titleReady: (state, getters) => {
        return state.config && (getters.isTextTitle || !!state.titleImgSize)
    },
    optionsReady: (state, getters) => {
        if (!state.config) {
            return false
        }
        const optionCount = getters.options.length
        let optionsReady = false
        for (let i = 0; i < optionCount; i++) {
            optionsReady = !!state.optionsImgSize[i]
            if (!optionsReady) {
                break
            }
        }
        return optionsReady
    },
    answerReady: (state) => state.config && !!state.answerImgSize,
    ready: (state, getters) => {
        return getters.titleReady && getters.optionsReady && getters.answerReady
    },
    bgImgUrl: (state, getters, rootState, rootGetters) => {
        if (state.config.backgroundImgName) {
            return constructLocalFileUrl(rootGetters, state.config.backgroundImgName)
        }
    },
    isImgTitle: state => state.config && state.config.title && state.config.title.type === 'img',
    isTextTitle: state => state.config && state.config.title && state.config.title.type === 'text',
    title: (state, getters, rootState, rootGetters) => {
        if (getters.isImgTitle && state.config.title.img) {
            const titleInfo = state.config.title.img
            return {
                imgUrl: constructLocalFileUrl(rootGetters, titleInfo.imgName),
                top: titleInfo.positionTop,
                left: titleInfo.positionLeft,
                scale: titleInfo.scale || 1
            }
        }
        if (getters.isTextTitle && state.config.title.text) {
            const titleInfo = state.config.title.text
            return {
                content: titleInfo.content,
                top: titleInfo.positionTop,
                left: titleInfo.positionLeft,
                fontSize: util.pxToVWNumber(titleInfo.fontSize * state.shrink),
                styles: titleInfo.styles
            }
        }
    },
    options: (state, getters, rootState, rootGetters) => {
        const optionsInfo = state.config.options
        if (!optionsInfo) {
            return []
        }
        return optionsInfo.map(option => {
            return {
                imgUrl: constructLocalFileUrl(rootGetters, option.imgName),
                top: option.positionTop,
                left: option.positionLeft,
                scale: option.scale || 1,
                translateX: option.translateX || 0,
                translateY: option.translateY || 0
            }
        })
    },
    answer: (state, getters, rootState, rootGetters) => {
        if (state.config && state.config.answer) {
            const answerInfo = state.config.answer
            return {
                bgImgUrl: constructLocalFileUrl(rootGetters, answerInfo.backgroundImgName),
                correctOptionIndex: answerInfo.correctOptionIndex,
                top: answerInfo.positionTop,
                left: answerInfo.positionLeft,
                scale: answerInfo.scale || 1
            }
        }
    }
}

const actions = {
    async syncConfig ({commit, getters, rootGetters}) {
        try {
            const configFileUrl = constructLocalFileUrl(rootGetters, 'config.json')
            const configBuffer = await util.loadLocalFile(configFileUrl)
            const config = util.arrayBufferToJson(configBuffer)
            // todo validate config
            commit(types.SET_STAGE_CONFIG, config)

            if (getters.isImgTitle) {
                // calc title img size
                util.calcImgSize(getters.title.imgUrl, getters.title.scale, (size) => {
                    commit(types.SET_STAGE_TITLE_IMG_SIZE, size)
                })
            }

            // calc options img size
            getters.options.forEach((option, optionIndex) => {
                util.calcImgSize(option.imgUrl, option.scale, (size) => {
                    commit(types.SET_STAGE_OPTION_IMG_SIZE, {optionIndex, size})
                })
            })

            // calc answer background img size
            util.calcImgSize(getters.answer.bgImgUrl, getters.answer.scale, (size) => {
                commit(types.SET_STAGE_ANSWER_IMG_SIZE, size)
            })

            return true
        } catch (err) {
            logService.log('sync config failed, err:', err)
            return false
        }
    },
    updateStageShrink ({commit}, shrink) {
        commit(types.SET_STAGE_SHRINK, shrink)
    }
}

const mutations = {
    [types.SET_STAGE_SHRINK] (state, shrink) {
        state.shrink = shrink
    },
    [types.SET_STAGE_CONFIG] (state, config) {
        state.config = config
    },
    [types.SET_STAGE_TITLE_IMG_SIZE] (state, size) {
        state.titleImgSize = size
    },
    [types.SET_STAGE_OPTION_IMG_SIZE] (state, {optionIndex, size}) {
        state.optionsImgSize[optionIndex] = size
        state.optionsImgSize = Array.from(state.optionsImgSize)
    },
    [types.SET_STAGE_ANSWER_IMG_SIZE] (state, size) {
        state.answerImgSize = size
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
    strict: process.env.NODE_ENV !== 'production'
}



