'use strict'
import * as webAppStatus from '../constants/web-app-status'
import { Buffer } from 'buffer'

const bizService = {
    // bizbase64payload
    decodePayload (base64Payload) {
        const buffer = Buffer.from(base64Payload, 'base64')
        const payload = JSON.parse(buffer.toString())
        payload.status = webAppStatus.fromValue(payload.status)
        return payload
    },
    validateBizPayload (payload, rootGetters) {
        if (!payload) {
            return false
        }
        const equalFields = ['appId', 'appVersion', 'roomId', 'keynotePageId', 'appConfigId']
        const equal = equalFields.every(field => rootGetters[field] === payload[field])
        const validStatus = webAppStatus.validStatus(payload.status)
        return equal && validStatus
    }
}

export default bizService



