'use strict'

import Vue from 'vue'
import environment from './environment'
import * as modes from '../constants/mode'
import VueResource from 'vue-resource'

Vue.use(VueResource)

const statService = Vue.resource(environment.apiPrefix + 'tutor-live-box-drag-drop/api/rooms{/roomId}/drag-boxed')

statService.getStudentsStat = function (mode) {
    switch (mode) {
        case modes.LIVE_CAST: {
            // todo 
            return Promise.resolve({})
        }
        case modes.PLAYBACK: {
            // do nothing
            return Promise.resolve({})
        }
        case modes.DEV: {
            const studentsMock = [{
                avatar: '1581f9c7bd15c55.jpg',
                nickname: 'Tom Jack',
                answer: 0
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 1
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 2
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '123',
                answer: 3
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 4
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 5
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 6
            }, {
                avatar: '1581f9c7bd15c55.jpg',
                nickname: '',
                answer: 7
            }]

            const completedCount = Math.ceil(Math.random() * 20)
            const totalCount = completedCount + 5
            const students = new Array(completedCount).fill(0).map(() => {
                const random = Math.floor(Math.random() * studentsMock.length)
                return studentsMock[random]
            })
            return Promise.resolve({totalCount, students})
        }
    }
}

export default statService



