'use strict'

function twoDigit (n) {
    return (`0${n}`).slice(-2)
}

export default function (datetime, referencedDateTime) {
    const after = datetime >= referencedDateTime
    const duration = Math.abs(datetime - referencedDateTime)
    const minutes = Math.floor(duration / 60000)
    const seconds = twoDigit(Math.round((duration % 60000) / 1000))
    return `${after ? '' : '-'}${minutes}:${seconds}`
}



