import Vue from 'vue'
import Router from 'vue-router'
import Entry from '../components/Entry'

Vue.use(Router)

export default new Router({
    routes: [
        {
            // query params include [appId, appVersion, appConfigId, roomId, userId, userRole, mode, keynotePageId, firstStrokePageId, withBiz]
            // mode  livecastplaybackdev
            // withBiz biz
            path: '/',
            name: 'entry',
            component: Entry
        }
    ]
})



