<template>
    <div class="web-app-container">
        <teacher-web-app v-if="isTeacher"></teacher-web-app>
        <student-web-app v-if="isStudent"></student-web-app>
    </div>
</template>

<script>
    import TeacherWebApp from './TeacherWebApp'
    import StudentWebApp from './StudentWebApp'
    import { mapGetters, mapActions } from 'vuex'
    import BoxBridge from '@tutor/box-bridge'

    export default {
        components: {
            StudentWebApp,
            TeacherWebApp
        },
        name: 'Entry',
        computed: {
            ...mapGetters([
                'isTeacher',
                'isStudent',
                'isLiveCastMode',
                'isPlaybackMode',
                'isDevMode'
            ])
        },
        methods: {
            ...mapActions('stage', [
                'syncConfig'
            ])
        },
        created () {
            if (this.isDevMode) {
                BoxBridge.Util.mockByCsi()
            }
            this.syncConfig()
        }
    }
</script>

<style lang="less" scoped>

    .web-app-container {
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 75%;

        div {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

</style>



