'use strict'
import * as types from '../mutation-types'
import { getToolbarItemsByWebAppStatus } from '../../constants/client-toolbar-item'
import bizService from '../../services/biz-service'
import boxService from '../../services/box-service'
import statService from '../../services/stat-service'
import logService from '../../services/log-service'
import clientService from '../../services/client-service'
import { Deferred } from '../../data/Deferred'

const state = {
    bizCreating: false, // 
    waitingBiz: false, // biz
    solutionStrokePageIdDeferred: undefined,
    interactionStartTime: undefined,
    totalCount: 0,
    students: []
}

const getters = {
    completedCount: (state) => {
        return state.students.length
    },
    answerDistribution: (state, getters, rootState, rootGetters) => {
        const optionCount = rootGetters['stage/options'].length
        const distributions = new Array(optionCount)
        for (let i = 0; i < optionCount; i++) {
            distributions[i] = {count: 0, ratio: 0}
        }
        state.students.forEach(student => {
            const answer = student.answer
            const distribution = distributions[answer]
            distribution.count = distribution.count + 1
            distribution.ratio = distribution.count / getters.completedCount
        })
        return distributions
    }
}

const mutations = {
    [types.TOGGLE_BIZ_CREATING] (state, bizCreating) {
        state.bizCreating = bizCreating
    },
    [types.TOGGLE_WAITING_BIZ] (state, waitingBiz) {
        state.waitingBiz = waitingBiz
    },
    [types.SET_SOLUTION_STROKE_PAGE_ID_DEFERRED] (state, deferred) {
        state.solutionStrokePageIdDeferred = deferred
    },
    [types.SET_INTERACTION_START_TIME] (state, time) {
        state.interactionStartTime = time
    },
    [types.SET_TOTAL_COUNT] (state, count) {
        state.totalCount = count
    },
    [types.SET_STUDENTS] (state, students) {
        state.students = students
    }
}

const actions = {
    async init ({commit, rootGetters}) {
        commit(types.TOGGLE_BIZ_CREATING, true)
        const mode = rootGetters.mode
        const roomId = rootGetters.roomId
        const appId = rootGetters.appId
        const appVersion = rootGetters.appVersion
        const appConfigId = rootGetters.appConfigId
        const keynotePageId = rootGetters.keynotePageId
        const questionStrokePageId = rootGetters.firstStrokePageId

        try {
            commit(types.TOGGLE_WAITING_BIZ, true)
            await boxService.init(mode, roomId, appId, appVersion, appConfigId, keynotePageId, questionStrokePageId)
            return true
        } catch (err) {
            logService.log('init web app failed, err:', err)
            commit(types.TOGGLE_WAITING_BIZ, false)
            return false
        } finally {
            commit(types.TOGGLE_BIZ_CREATING, false)
        }
    },
    async syncBiz ({commit, rootState, rootGetters}, biz) {
        const root = {root: true}
        commit(types.TOGGLE_BIZ_SYNCING, true, root)
        try {
            const payload = bizService.decodePayload(biz.payload)
            if (!bizService.validateBizPayload(payload, rootGetters)) {
                logService.log('biz is invalid')
                return false
            }

            commit(types.SET_QUESTION_STROKE_PAGE_ID, payload.questionStrokePageId, root)
            commit(types.SET_SOLUTION_STROKE_PAGE_ID, payload.solutionStrokePageId, root)
            commit(types.SET_STATUS, payload.status, root)

            return true
        } catch (err) {
            logService.log('sync biz failed, err:', err)
            return false
        } finally {
            commit(types.TOGGLE_WAITING_BIZ, false)
            commit(types.TOGGLE_BIZ_SYNCING, false, root)
        }
    },
    async toggleClientWidgetsEnabledAndStrokePageVisible ({rootState, rootGetters}) {
        try {
            const {enabledIds, disabledIds} = getToolbarItemsByWebAppStatus(rootState.status)
            await clientService.toggleToolbarItemsEnabled(enabledIds, disabledIds)
            if (rootGetters.isBeforeInteraction) {
                await clientService.toggleForumEnabled(true)
                await clientService.toggleStrokePageVisible(rootState.questionStrokePageId, true)
            } else if (rootGetters.isInteracting) {
                await clientService.toggleForumEnabled(false)
                await clientService.toggleStrokePageVisible(rootState.questionStrokePageId, false)
            } else if (rootGetters.isAfterInteractionQuestion) {
                await clientService.toggleForumEnabled(true)
                await clientService.toggleStrokePageVisible(rootState.questionStrokePageId, true)
                await clientService.toggleStrokePageVisible(rootState.solutionStrokePageId, false)
            } else if (rootGetters.isAfterInteractionSolution) {
                await clientService.toggleForumEnabled(true)
                await clientService.toggleStrokePageVisible(rootState.questionStrokePageId, false)
                await clientService.toggleStrokePageVisible(rootState.solutionStrokePageId, true)
            }
            return true
        } catch (err) {
            logService.log('toggle client widget enabled and stroke page visible failed, err:', err)
            return false
        }
    },
    async createSolutionStrokePage ({commit, state, rootState}) {
        if (rootState.solutionStrokePageId) {
            return true
        }
        let defer = state.solutionStrokePageIdDeferred
        try {
            if (!defer) {
                defer = new Deferred()
                commit(types.SET_SOLUTION_STROKE_PAGE_ID_DEFERRED, defer)
                await clientService.createStokePage()
            }
            const solutionStrokePageId = await defer.promise
            commit(types.SET_SOLUTION_STROKE_PAGE_ID, solutionStrokePageId, {root: true})
            return true
        } catch (err) {
            logService.log('create solution stroke page failed, err:', err)
            return false
        } finally {
            commit(types.SET_SOLUTION_STROKE_PAGE_ID_DEFERRED, undefined)
        }
    },
    onStrokePageCreated ({state, rootState}, currentStrokePageIds) {
        if (rootState.solutionStrokePageId || !state.solutionStrokePageIdDeferred) {
            logService.log(`ignore strokePageCreated event because solutionStrokePageId is exist or deferred is null`)
            return
        }
        if (!currentStrokePageIds || !currentStrokePageIds.length) {
            logService.log(`ignore strokePageCreated event because currentStrokePageIds is empty`)
            return
        }
        if (!currentStrokePageIds.includes(rootState.questionStrokePageId)) {
            logService.log(`ignore strokePageCreated event because can't find questionStrokePageId`)
            return
        }
        const latestStrokePageId = currentStrokePageIds[currentStrokePageIds.length - 1]
        if (rootState.questionStrokePageId !== latestStrokePageId) {
            state.solutionStrokePageIdDeferred.resolve(latestStrokePageId)
        } else {
            const msg = 'the latest stroke page is question stroke page'
            state.solutionStrokePageIdDeferred.reject(msg)
        }
    },
    async startInteraction ({commit, rootGetters}) {
        commit(types.TOGGLE_BIZ_CREATING, true)
        try {
            commit(types.TOGGLE_WAITING_BIZ, true)
            await boxService.startInteraction(rootGetters.mode, rootGetters.roomId, rootGetters.keynotePageId)
            commit(types.SET_INTERACTION_START_TIME, new Date().valueOf())
            return true
        } catch (err) {
            logService.log('start interaction failed, err:', err)
            commit(types.TOGGLE_WAITING_BIZ, false)
            return false
        } finally {
            commit(types.TOGGLE_BIZ_CREATING, false)
        }
    },
    async endInteraction ({dispatch, commit, rootState, rootGetters}) {
        commit(types.TOGGLE_BIZ_CREATING, true)
        try {
            commit(types.TOGGLE_WAITING_BIZ, true)
            if (!await dispatch('createSolutionStrokePage')) {
                return false
            }
            await boxService.endInteraction(rootGetters.mode, rootGetters.roomId, rootGetters.keynotePageId, rootState.solutionStrokePageId)
            return true
        } catch (err) {
            logService.log('end interaction failed, err:', err)
            commit(types.TOGGLE_WAITING_BIZ, false)
            return false
        } finally {
            commit(types.TOGGLE_BIZ_CREATING, false)
        }
    },
    async jumpToQuestion ({commit, rootGetters}) {
        commit(types.TOGGLE_BIZ_CREATING, true)
        try {
            commit(types.TOGGLE_WAITING_BIZ, true)
            await boxService.jumpToQuestion(rootGetters.mode, rootGetters.roomId, rootGetters.keynotePageId)
            return true
        } catch (err) {
            logService.log('jump to question failed, err:', err)
            commit(types.TOGGLE_WAITING_BIZ, false)
            return false
        } finally {
            commit(types.TOGGLE_BIZ_CREATING, false)
        }
    },
    async jumpToSolution ({commit, rootGetters}) {
        commit(types.TOGGLE_BIZ_CREATING, true)
        try {
            commit(types.TOGGLE_WAITING_BIZ, true)
            await boxService.jumpToSolution(rootGetters.mode, rootGetters.roomId, rootGetters.keynotePageId)
            return true
        } catch (err) {
            logService.log('jump to solution failed, err:', err)
            commit(types.TOGGLE_WAITING_BIZ, false)
            return false
        } finally {
            commit(types.TOGGLE_BIZ_CREATING, false)
        }
    },
    async getStudentsStat ({commit, rootGetters}) {
        try {
            const studentsStat = await statService.getStudentsStat(rootGetters.mode)
            commit(types.SET_TOTAL_COUNT, studentsStat.totalCount)
            commit(types.SET_STUDENTS, studentsStat.students)
            return true
        } catch (err) {
            logService.log('get students stat failed, err:', err)
            return false
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
    strict: process.env.NODE_ENV !== 'production'
}



