'use strict'

import * as webAppStatus from './web-app-status'

const UNDO = 0
const REDO = 1
const CLEAR = 2
const INSERT_BLANK_PAGE = 3
const GENERAL_BRUSH = 4
const INTELLIGENT_BRUSH = 5
const COLOR = 6
const ERASER = 7
const FAST_QUIZ = 8
const PLAY_VIDEO = 9
const MICROPHONE = 10

const webAppStatusToToolbarItems = new Map([
    [
        webAppStatus.BEFORE_INTERACTION,
        {
            enabledIds: [UNDO, REDO, CLEAR, GENERAL_BRUSH, INTELLIGENT_BRUSH, COLOR, ERASER, PLAY_VIDEO, MICROPHONE],
            disabledIds: [INSERT_BLANK_PAGE, FAST_QUIZ]
        }
    ],
    [
        webAppStatus.INTERACTING,
        {
            enabledIds: [],
            disabledIds: [UNDO, REDO, CLEAR, INSERT_BLANK_PAGE, GENERAL_BRUSH, INTELLIGENT_BRUSH, COLOR, ERASER, FAST_QUIZ, PLAY_VIDEO, MICROPHONE]
        }
    ],
    [
        webAppStatus.AFTER_INTERACTION_QUESTION,
        {
            enabledIds: [UNDO, REDO, CLEAR, GENERAL_BRUSH, INTELLIGENT_BRUSH, COLOR, ERASER, PLAY_VIDEO, MICROPHONE],
            disabledIds: [INSERT_BLANK_PAGE, FAST_QUIZ]
        }
    ],
    [
        webAppStatus.AFTER_INTERACTION_SOLUTION,
        {
            enabledIds: [UNDO, REDO, CLEAR, GENERAL_BRUSH, INTELLIGENT_BRUSH, COLOR, ERASER, PLAY_VIDEO, MICROPHONE],
            disabledIds: [INSERT_BLANK_PAGE, FAST_QUIZ]
        }
    ]
])

export function getToolbarItemsByWebAppStatus (status) {
    return webAppStatusToToolbarItems.get(status)
}



