<template>
    <div class="student-stage-container">
        <student-web-app-stage></student-web-app-stage>
    </div>
</template>

<script>
    import BoxBridge from '@tutor/box-bridge'
    import clientService from '../services/client-service'
    import * as webAppEvents from '../constants/web-app-event'
    import * as clientEvents from '../constants/client-event'
    import { mapState, mapGetters, mapActions } from 'vuex'
    import StudentWebAppStage from './StudentWebAppStage'

    export default {
        name: 'student-web-app',
        components: {
            StudentWebAppStage
        },
        computed: {
            ...mapState([
                'firstBizSynced'
            ]),
            ...mapGetters('stage', {
                stageReady: 'ready'
            })
        },
        methods: {
            ...mapActions([
                'setFirstBizSynced'
            ]),
            ...mapActions('student', [
                'syncBiz',
                'toggleClientWidgetsEnabledAndStrokePageVisible'
            ])
        },
        watch: {
            stageReady: function (isStageReady) {
                if (!isStageReady) {
                    return
                }
                clientService.emit(webAppEvents.JS_READY)
            }
        },
        mounted () {
            const ClientApi = BoxBridge.ClientApi
            ClientApi.on(clientEvents.ACTIVE, async () => {
                await this.toggleClientWidgetsEnabledAndStrokePageVisible()
            })

            ClientApi.on(clientEvents.DESTROY, () => {
                ClientApi.destroy()
                clientService.emit(webAppEvents.END)
            })

            ClientApi.on(clientEvents.BIZ_CREATED, async (biz) => {
                await this.syncBiz(biz)
                if (this.firstBizSynced) {
                    this.toggleClientWidgetsEnabledAndStrokePageVisible()
                } else {
                    this.setFirstBizSynced()
                    clientService.emit(webAppEvents.READY)
                }
            })
        }
    }
</script>

<style lang="less" scoped>

    .student-stage-container {
        width: 100%;
        height: 100%;
    }

</style>



