<template>
    <div class="stage-answer-container" :style="containerStyles">
        <div class="stage-answer-bg-container" :style="bgStyles"></div>
        <div class="correct-answer-container" v-if="isAfterInteractionSolution" :style="correctStyles">
        </div>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'stage-answer',
        computed: {
            ...mapState('stage', [
                'answerImgSize',
                'optionsImgSize'
            ]),
            ...mapGetters('stage', [
                'answerReady',
                'answer',
                'options'
            ]),
            ...mapGetters([
                'isAfterInteractionSolution'
            ]),
            containerStyles () {
                const styles = {}
                if (this.answerReady) {
                    styles.top = this.answer.top
                    styles.left = this.answer.left
                    styles.width = this.answerImgSize.width
                    styles.height = this.answerImgSize.height
                }
                return styles
            },
            bgStyles () {
                const styles = {}
                if (this.answerReady) {
                    styles.backgroundImage = `url('${this.answer.bgImgUrl}')`
                }
                return styles
            },
            correctStyles () {
                const styles = {}
                if (this.answerReady) {
                    const correctOptionIndex = this.answer.correctOptionIndex
                    const correctOption = this.options[correctOptionIndex]
                    const correctOptionImgSize = this.optionsImgSize[correctOptionIndex]
                    const correctOptionImgSizeFixed = this.fixCorrectAnswerSize(this.answerImgSize, correctOptionImgSize)
                    styles.backgroundImage = `url('${correctOption.imgUrl}')`
                    styles.width = correctOptionImgSizeFixed.width
                    styles.height = correctOptionImgSizeFixed.height
                    styles.transform = `translate(${correctOption.translateX}, ${correctOption.translateY})`
                }
                return styles
            }
        },
        methods: {
            parseSizeNum (size) {
                return {
                    width: +size.width.substring(0, size.width.length - 1),
                    height: +size.height.substring(0, size.height.length - 1)
                }
            },
            fixCorrectAnswerSize (answerImgSize, correctOptionImgSize) {
                // stage
                const answerImgSizeNum = this.parseSizeNum(answerImgSize)
                const correctOptionImgSizeNum = this.parseSizeNum(correctOptionImgSize)
                return {
                    width: `${100 * correctOptionImgSizeNum.width / answerImgSizeNum.width}%`,
                    height: `${100 * correctOptionImgSizeNum.height / answerImgSizeNum.height}%`
                }
            }
        }
    }
</script>

<style lang="less" scoped>

    .stage-answer-container {
        position: absolute;

        .stage-answer-bg-container {
            position: absolute;
            width: 100%;
            height: 100%;
            background-repeat: no-repeat;
            background-size: 100% 100%;
        }

        .correct-answer-container {
            position: absolute;
            overflow: auto;
            margin: auto;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-repeat: no-repeat;
            background-size: 100% 100%;
        }
    }

</style>



