<template>
    <div class="stage-container" :style="{backgroundImage: backgroundImage}">
        <stage-title></stage-title>
        <stage-answer></stage-answer>
        <stage-option v-for="(option,index) in options" :key="index" :index="index"></stage-option>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import StageTitle from './StageTitle'
    import StageAnswer from './StageAnswer'
    import StageOption from './StageOption'

    export default {
        name: 'teacher-web-app-stage',
        components: {
            StageAnswer,
            StageTitle,
            StageOption
        },
        computed: {
            ...mapGetters('stage', [
                'bgImgUrl',
                'options'
            ]),
            backgroundImage () {
                return this.bgImgUrl ? `url('${this.bgImgUrl}')` : ''
            }
        }
    }
</script>

<style lang="less" scoped>

    .stage-container {
        position: relative;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }

</style>



